.dx-overlay-content{
	.dx-popup-content.dx-popup-content-report{
		padding: 0;
		margin-top: 4px;
		.creator_table_cell{
			&.field-number, &.field-currency{
				/* å­—æ®µé å³å¯¹é½ */
				display: block;
			}
			.creator_table_cell{
				/* lookupã€master_detailç±»åž‹æ•°ç»„ï¼Œæ˜¾ç¤ºä¸ºä¸€è¡Œ */
				display: inline;
				.cell-container{
					display: inline;
				}
			}
		}
		.dx-datagrid-headers{
			.dx-datagrid-table{
				.dx-row > td{
					padding-top: 7px;
					padding-bottom: 7px;
				}
			}
		}
		.dx-datagrid-content{
			.dx-datagrid-table{
				.dx-row>td{
					padding-top: 7px;
					padding-bottom: 7px;
					&:first-of-type{
						padding-left: 24px;
					}
					&:last-of-type{
						padding-right: 24px;
					}
				}
			}
		}
		.cell-wrap{
			.slds-truncate{
				overflow-wrap: break-word;
				word-wrap: break-word;
				-webkit-hyphens: none;
				-ms-hyphens: none;
				hyphens: none;
				white-space: normal;
				overflow: hidden;
				position: relative;
				max-height: 3.65625rem;
			}
	
			.slds-truncate:after {
				content: "";
				position: absolute;
				top: 2.25rem;
				bottom: 0;
				right: 0;
				width: 25%;
				background: linear-gradient(to right, rgba(255,255,255,0.4) 0%,rgba(255,255,255,1) 69%);
			}
		}
	}
}